<template>
  <div class="index">
    <el-container>
      <el-aside width="200px">
        <!-- router  是否使用 vue-router 的模式，启用该模式会在激活导航时以 index 作为 path 进行路由跳转 -->
         <el-menu
          default-active="/"
      class="el-menu-vertical-demo"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b" :unique-opened="true" router>
      <el-menu-item index="/">
            <i class="el-icon-setting"></i>
            <span slot="title">首页</span>
      </el-menu-item>
      <template  v-for="item in user.menus" >
          <!-- 此处遍历的是有children,即有目录 -->
          <el-submenu :index="item.title" v-if="item.children" :key="item.id">
              <template slot="title">
                <i :class="item.icon"></i>
                <span>{{item.title}}</span>
              </template>
              <template v-for="i in item.children">
                <el-menu-item :index="'/'+i.url" :key="i.id">{{i.title}}</el-menu-item>
              </template>
        </el-submenu>
        <!-- 此处显示没有children -->
         <el-menu-item :key="item.id" v-if="!item.children" :index="'/'+item.url">{{item.title}}</el-menu-item>
      </template>

      <!-- <el-submenu index="2">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span>商城管理</span>
        </template>
          <el-menu-item index="/cate">商品分类</el-menu-item>
          <el-menu-item index="/spec">商品规格</el-menu-item>
          <el-menu-item index="/goods">商品管理</el-menu-item>
          <el-menu-item index="/member">会员管理</el-menu-item>
          <el-menu-item index="/banner">轮播图管理</el-menu-item>
          <el-menu-item index="/seckill">秒杀活动</el-menu-item>
      </el-submenu> -->
    </el-menu>
      </el-aside>
      <el-container>
        <el-header height="80px">
          {{user.username}}
          <el-button type="primary" @click="logout">退出</el-button>
        </el-header>
        <el-main>
          <!-- 使用面包屑 -->
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>{{$route.name}}</el-breadcrumb-item>
          </el-breadcrumb>
          <!-- 二级路由出口 -->
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
export default {
  computed:{
    ...mapGetters({
      'user':'user'
    })
  },
  methods:{
    ...mapActions({
      "requestUser":"userActions"
    }),
    logout(){
      this.requestUser(null);
      this.$router.push('/login')
      return
    }
  },
  mounted(){
    if(this.user ===  null){
      this.$router.push('/login');
      return
    }
  }
}
</script >

<style scoped>
.el-aside{
  height:100vh;
  background:#20222A;
  color:white;
}
.el-header{
  background: #B3C0D1;
  line-height: 80px;
  text-align: right;
}
.el-button{
  margin-right:20px;
}
</style>



